import React from 'react'
import { Avatar, Card, ListItem } from 'react-native-elements'
import { Text, Linking } from 'react-native'
import { checkAddr } from 'helpers/address'
import { H3 } from '@expo/html-elements'
import { EXPLORER_MAP } from 'staker-freenodes'

const AddrTx = ({ term, chainId }) => {
  const chainExplorer = EXPLORER_MAP[chainId]
  if (!chainExplorer) return <H3>Explorer Not found</H3>

  const [isAddress, isTx] = checkAddr(term)
  if (isAddress) {
    const explorers = Object.keys(chainExplorer)
    return (
      <>
        {explorers &&
          explorers.map((key) => (
            <ListItem key={chainExplorer[key].name}>
              <Avatar rounded title='üîé' />
              <ListItem.Content>
                <ListItem.Title>
                  <Text
                    onPress={() =>
                      Linking.openURL(chainExplorer[key].address(addr))
                    }
                  >
                    {chainExplorer[key].name}
                  </Text>
                </ListItem.Title>
              </ListItem.Content>
            </ListItem>
          ))}
      </>
    )
  }
  if (isTx) {
    const explorers = Object.keys(chainExplorer)
    return (
      <>
        {explorers &&
          explorers.map((key) => (
            <ListItem key={chainExplorer[key].name}>
              <Avatar rounded title='üîé' />
              <ListItem.Content>
                <ListItem.Title>
                  <Text
                    onPress={() => Linking.openURL(chainExplorer[key].tx(addr))}
                  >
                    {chainExplorer[key].name}
                  </Text>
                </ListItem.Title>
              </ListItem.Content>
              <ListItem.Chevron />
            </ListItem>
          ))}
      </>
    )
  }
  return <H3>Invalid Address</H3>
}

export const Explorers = ({ chainId, term }) => {
  return (
    <>
      {/* <Card>
      <Card.Title>üîé {CHAIN_MAP[chainId]} Chain Explorer</Card.Title> */}
      <Card.Divider />
      <AddrTx term={term} chainId={chainId} />
      {/* </Card> */}
    </>
  )
}

export default Explorers
